import { Box, Stack, TextField, Typography } from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { useState } from "react";
import ValidationField from "./ValidationField";
import PasswordRange from "./PasswordRange";

const PasswordValidator = () => {
  const [password, setPassword] = useState("");
  const [range, setRange] = useState([8, 15]);
  return (
    <Box p={2}>
      <Stack direction="row" justifyContent="center">
        <Typography variant="h4" fontWeight="bold">
          Password Validator
        </Typography>
      </Stack>
      <Stack
        pt={2}
        minWidth={0}
        spacing={1}
        alignItems="flex-start"
        direction="column"
      >
        <TextField
          fullWidth
          size="small"
          label="Enter password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <Stack direction="row" spacing={2} alignItems="center">
          {password.length >= range[0] && password.length <= range[1] && (
            <CheckCircleIcon color="success" />
          )}
          <Typography
            width="60%"
            color={
              password.length >= range[0] && password.length <= range[1]
                ? "green"
                : "black"
            }
            variant="body1"
          >
            Password Range (min: {range[0]}, max: {range[1]})
          </Typography>
          <PasswordRange
            value={range}
            handleChange={(value) => setRange(value)}
          />
        </Stack>
        <ValidationField
          checkPassed={password !== password.toLowerCase()}
          title="Password contains capital letter"
        />
        <ValidationField
          checkPassed={/\d/.test(password)}
          title="Password contains a number"
        />
        <ValidationField
          checkPassed={password !== password.toUpperCase()}
          title="Password contains small letter"
        />
      </Stack>
    </Box>
  );
};

export default PasswordValidator;
